/* Base Styles for Schedule Card */
.schedule-card {
  position: relative;
  align-content: center;
  justify-items: center;
  height: 5.3rem;
  width: 4.4rem;
  border-radius: 10px;
  background: none;
  cursor: pointer;
  overflow: hidden;
}

.schedule-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: -webkit-linear-gradient(to bottom, rgba(170, 225, 255, 0.6) 0%, rgb(230, 245, 255) 100%);
  background: -moz-linear-gradient(to bottom, rgba(170, 225, 255, 0.6) 0%, rgb(230, 245, 255) 100%);
  background: -o-linear-gradient(to bottom, rgba(170, 225, 255, 0.6) 0%, rgb(230, 245, 255) 100%);
  background: linear-gradient(to bottom, rgba(170, 225, 255, 0.6) 0%, rgb(230, 245, 255) 100%);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  z-index: 1;
}

.schedule-card > * {
  position: relative;
  z-index: 2;
}

.schedule-card:hover::before,
.schedule-card.chosen::before {
  opacity: 1;
}

.schedule-card:hover,
.schedule-card.chosen {
  border: 1.75px solid var(--clr-highlight-primary-700);
  transition: all 0.3s ease-in-out;
}

.hidden-element {
  opacity: 0;
  transition: all 0.2s ease;
}

.hover-parent:hover .hidden-element {
  opacity: 1;
}

/* Topbar */
.topbar {
  height: 10vh;
  width: 100%;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-auto-flow: column;
  align-items: center;
  position: relative;
  padding: 0em 5em 0em 5em;
}

.logoContainer {
  display: flex;
  flex-direction: row;
  gap: 15px;
  height: auto;
  width: fit-content;
  align-items: center;
}

.logo-text {
  position: relative;
  top: 0;
  right: 0;
  color: #1B2B32;
  font-size: 2.2rem;
  font-weight: 500;
}

/* End of Logo*/
.topbarItems {
  height: 100%;
  max-width: 100%;
  display: grid;
  justify-self: end;
  align-items: center;
  text-align: center;
  grid-template-columns: repeat(auto-fit, minmax(min-content, 1fr));
  grid-auto-flow: column;
  overflow: hidden;
  gap: 2.5rem;
  transition: all 0.2s ease-in;
}

.topbarItems .topbarItem {
  text-decoration: none;
  outline: none;
  color: black;
}

/* End of Navbar */
/* Sidebar */
.sidebar-container {
  grid-area: sidebar;
  grid-row: 1/-1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100%;
  border-radius: 20px;
  background-image: linear-gradient(to bottom, rgb(27, 43, 50) 0%, rgb(55, 78, 95) 50%, rgb(27, 43, 50) 100%);
}

.sidebar {
  display: flex;
  align-items: center;
  height: 100%;
  color: #E3E3E3;
  font-size: 12px;
}

.sidebar ul {
  display: flex;
  flex-direction: column;
  gap: 2.5em;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  width: 3rem;
  border-radius: 100%;
  background: transparent;
  transition: all 0.2s ease-in;
}

.indicator:active,
.indicator:hover {
  background: #E3E3E3;
}

.indicator:active svg,
.indicator:hover svg {
  fill: #1B2B32;
}

.nav-item {
  height: fit-content;
  width: fit-content;
  background: transparent;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.nav-item:hover,
.nav-item.active {
  background: var(--clr-neutral-1100);
}

.nav-item:hover svg,
.nav-item.active svg {
  fill: var(--clr-dark-100);
}

/* End of Sidebar */
/* Navbar for Dashboard */
.dashboardContainer .topbar {
  grid-area: topbar;
  grid-column: 1/-1;
  background: #FAFAFA;
  border-radius: 20px 20px 0px 0px;
  box-shadow: 0px 0px 5px 0.1px rgba(0, 0, 0, 0.25);
  padding: 0em 3em 0em 3em;
}

/* End of Navbar for Dashboard */
/* Navbar for Page Section */
.sectionHeader .topbar {
  margin: 20px 0px 20px 0px;
}

.sectionHeader .logoContainer {
  gap: 20px;
}

.sectionHeader .topbar .logo-text {
  font-size: 45px;
  color: #3F6BB3;
}

.sectionHeader .topbarItems .customBtn {
  color: white;
  background: #3F6BB3;
  border-radius: 5px;
  padding: 10px 15px 10px 15px;
}

.sectionHeader .topbarItem:hover {
  font-weight: 500;
  color: rgb(80, 150, 255);
}

.sectionHeader .topbarItems .customBtn:hover {
  color: white;
  box-shadow: 0px 0px 5px 0.5px rgb(80, 150, 255);
}

/* End of Navbar for Page Section */
/* Default Style for Progress Bar */
.progress-indicator {
  height: 4.5rem;
  width: 4.5rem;
  color: var(--clr-neutral-1000);
  background: var(--clr-neutral-1100);
  text-align: center;
  align-items: center;
  justify-items: center;
  border: 3.5px solid var(--clr-line-color, var(--clr-neutral-1000));
  border-radius: 50%;
  transition: all 0.25s ease;
  z-index: 1;
}
.progress-indicator.active {
  color: var(--clr-secondary-400);
  border: 3.5px solid var(--clr-line-color, var(--clr-secondary-400));
}

.progress-step {
  padding: 0;
  text-align: center;
  align-items: center;
  color: var(--clr-neutral-1000);
}
.progress-step.active {
  color: var(--clr-secondary-400);
}

.stepbar-wrapper {
  position: relative;
  height: 0.25rem;
  width: 100%;
  padding: 0;
  transform: translate(2rem, 2.2rem);
}

.step-bar {
  z-index: 0;
  height: 0.25rem;
  width: 100%;
  background: var(--clr-neutral-1000);
  position: absolute;
  transition: width 0.3s ease;
}
.step-bar.active {
  background: var(--clr-secondary-400);
}

.step-meter {
  z-index: 0;
  height: 0.25rem;
  width: 0%;
  background: var(--clr-secondary-400);
  position: absolute;
  transition: width 0.3s ease;
}

/* End of Default Styles for Progress Bar*/
.circle-background {
  fill: none;
}

.circle-progress {
  fill: none;
  stroke-linejoin: round;
}

.circle-text {
  font-weight: 600;
}

/* Button: Default Style */
.button {
  height: fit-content;
  width: fit-content;
  padding: 0.5em 1em 0.5em 1em;
  border-radius: 5px;
  text-align: center;
  color: white;
  background: var(--clr-primary-400);
  transition: all 0.25s ease;
}

.button[disabled] {
  background: var(--clr-neutral-1000) !important;
}

/* End of Default Style */
.scrollable {
  overflow-y: auto;
}

/* Search Bar: Default Styles */
.input-bar {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  height: fit-content;
  max-width: 100%;
  background: #F5F5F5;
  border-radius: 20px;
  padding: 0rem 0.8rem 0rem 0.8rem;
}

.input-bar .search-icon {
  visibility: hidden;
  width: 0;
}

.input-bar .send-icon {
  visibility: hidden;
  width: 0;
}

.input-bar input {
  height: 100%;
  width: 90%;
  outline: none;
  border: none;
  background: none;
